import echarts from 'echarts'

export default data => ({
  tooltip: {
    show: true,
    trigger: 'item'
  },
  series: {
    name: data.name,
    type: 'gauge',

    startAngle: 180,
    endAngle: 0,

    tooltip: {
      formatter: '{a}',
      textStyle: {
        fontSize: 26,
        color: '#FFF'
      }
    },

    // 标题
    title: {
      show: false
    },

    // 值
    detail: {
      show: false
    },
    splitLine: {
      show: false
    },
    axisLine: {
      lineStyle: {
        color: [
          [0, 'transparent'],
          [0, 'transparent'],
          [1, new echarts.graphic.LinearGradient(
            0, 0, 1, 0,
            [
              { offset: 0, color: '#0d78d8' },
              { offset: 0.5, color: '#9dc045' },
              { offset: 1, color: '#ffd200' }
            ]
          )]
        ],
        width: 20
      }
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      show: false
    },

    // 指针样式
    itemStyle: {
      color: '#959595'
    },
    data: [{
      name: data.IP,
      value: data.value
    }]
  }
})



