export default {
  'url': '/dataSecurity/bastion/traceSource/:hostid/:userid/:groupid',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|5-10': [
        {
          'username': '@cname',
          'time': '@datetime',
          'operate': '@operate'
        }
      ]
    }
  }
}



