import './index.css'
import $ from 'jquery'
import echarts from 'echarts'
import { max, take } from 'lodash'
import { fetch } from '@/util/request'
import gaugeHbs from './templates/gauge.hbs'
import modelItemHbs from './templates/modelItem.hbs'
import tableHbs from './templates/table.hbs'
import gaugeOpt from './options/gaugeOpt'

export default class Database {
  render() {
    // 渲染数据库操作次数
    fetch('fetchDatabaseOperateCount', data => {
      $('#database .count span').eq(1).html(data.value)
    })

    // 渲染高频访问TOP5
    fetch('fetchDatabaseVisitTop5', data => {
      for (let [i, d] of data.list.entries()) {
        $('#databaseGaugeGroup').append(gaugeHbs(d))

        const chartDom = $('#databaseGaugeGroup .gauge-item').eq(i).find('.chart')[0]
        const chart = echarts.init(chartDom)
        const maxVal = max(data.list.map(v => v.value))
        console.log(d)
        const opt = gaugeOpt(d)
        opt.series.max = maxVal
        chart.setOption(opt)
      }
    })

    // 渲染模型列表
    fetch('fetchDatabaseModelList', dataSource => {
      const modelList = $('#database').find('.model-list')

      for (let [i, data] of take(dataSource.list, 4).entries()) {
        modelList.append(modelItemHbs(data))

        // 制作柱形图
        const maxVal = max(dataSource.list.map(v => v.value))
        const rate = 100 - Math.floor(data.value / maxVal * 100)

        const modelItem = modelList.find('.model-item').eq(i)
        const content = modelItem.find('.content')
        const icon = modelItem.find('.icon')
        const dialog = modelItem.find('.dialog')
        const bar = content.find('.bar')
        const cover = bar.find('span').eq(1)

        cover.css({ width: `${rate}%` })

        // 为图标绑定点击事件
        icon.off('click').on('click', e => {
          const id = e.currentTarget.dataset.id

          if (dialog.hasClass('hide')) {
            modelItem.siblings().find('.dialog').addClass('hide')
            dialog.removeClass('hide')

            fetch('fetchDatabaseModelIntro', { id }, d => {
              dialog.find('p').html(d.content)
            })
          } else {
            dialog.addClass('hide')
          }
        })

        // 为列表绑定点击事件
        content.off('click').on('click', e => {
          const id = e.currentTarget.dataset.id

          // 点击后, 需要高亮显示列表
          modelItem.siblings().find('.content').removeClass('emphasis')
          modelItem.siblings().find('.icon').removeClass('emphasis')

          content.addClass('emphasis')
          icon.addClass('emphasis')

          // 点击后渲染表格
          fetch('fetchDatabaseModelWarning', { id }, tableDataSource => {
            console.log(tableDataSource)
            const tableContainer = $('#database .model-table')
            tableContainer
              .empty()
              .append(tableHbs(take(tableDataSource.list, 4)))
              if(tableDataSource.list.length === 0) {
                $('.model-table').append('<span class="com-hint">无告警信息</span>')
              }
          })
        })

      }
    })
      // 触发点击事件
      .then(() => {
        const modelList = $('#database').find('.model-list')
        const modelItem = modelList.find('.model-item').eq(0)
        const content = modelItem.find('.content')

        content.click()
      })
  }
}



