import echarts from 'echarts'
import barCircleSrc from '../images/bar-circle.png'

export default (data, domain) => ({
  title: {
    show: false
  },
  tooltip: {
    show: true,
    formatter: '{b}',
    textStyle: {
      fontSize: 26,
      color: '#FFF'
    }
  },
  legend: {
    show: false
  },
  grid: {
    left: '0%',
    right: '0%',
    top: '3%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: {
    type: 'category',
    axisLine: {
      lineStyle: {
        width: 2,
        color: '#FFF'
      }
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      color: '#a5b0ff',
      fontSize: 26,
      margin: 12
    },
    data: domain
  },
  yAxis: {
    type: 'value',
    axisLine: {
      lineStyle: {
        type: 'dashed',
        color: '#a9b0c2'
      }
    },
    axisTick: {
      show: false
    },
    axisLabel: {
      fontSize: 22,
      color: '#a5b0ff'
    },
    splitLine: {
      lineStyle: {
        type: 'dashed',
        color: '#3f58a6'
      }
    }
  },
  series: [
    {
      data,
      type: 'pictorialBar',
      name: 'bar',
      symbol: 'triangle',
      symbolOffset: ['50%', 0],
      barWidth: 40,
      itemStyle: {
        color: new echarts.graphic.LinearGradient(
          0, 0, 0, 1,
          [
            { offset: 0, color: '#54fbed' },
            { offset: 1, color: '#fdd53a' }
          ]
        )
      },
      emphasis: {
        itemStyle: {
          color: new echarts.graphic.LinearGradient(
            0, 0, 0, 1,
            [
              { offset: 0, color: '#ff2d01' },
              { offset: 1, color: '#fff' }
            ]
          )
        }
      }
    },
    {
      data,
      type: 'pictorialBar',
      name: 'innerBar',
      symbol: 'triangle',
      symbolOffset: [30, 0],
      barWidth: 20,
      itemStyle: {
        color: new echarts.graphic.LinearGradient(
          0, 0, 0, 1,
          [
            { offset: 0, color: '#54fbed' },
            { offset: 0.3, color: '#d8f8df' },
            { offset: 1, color: '#FFF' }
          ]
        )
      }
    },
    {
      data,
      type: 'pictorialBar',
      name: 'symbol',
      symbol: `image://${barCircleSrc}`,
      symbolPosition: 'end',
      symbolOffset: [-10, -5],
      symbolSize: [18, 18]
    }
  ]
})



