export default {
  'url': '/dataSecurity/database/modelWarning/:id',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|0': [
        {
          'company': '@company',
          'username': '@cname',
          'sourceIP': '@ip',
          'targetIP': '@ip',
          'status': '@status',
          'time': '@datetime',
          'type': '@type',
          'dangerlevel': '@dangerlevel'
        }
      ]
    }
  }
}



