export default {
  'url': '/dataSecurity/bastion/visiterList/:hostid',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|10-20': [
        {
          'id': '@id',
          'groupId': '@id',
          'username': '@cname',
          'company': '@company',
          'operate': '@operate'
        }
      ]
    }
  }
}



