export default {
  'url': '/dataSecurity/bastion/loglist',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': {
      'list|10-20': [
        {
          'time': '@datetime',
          'username': '@cname',
          'logIP': '@ip',
          'sourceIP': '@ip',
          'hostname': '@name',
          'operate': '@operate',
          'result': '@status'
        }
      ]
    }
  }
}



